.sideDrawer{position:fixed;width:100%;height:100%;left:0;top:64px;z-index:200;background:#fafafa;box-sizing:border-box;-webkit-transition:-webkit-transform .5s ease-out;transition:-webkit-transform .5s ease-out;transition:transform .5s ease-out;transition:transform .5s ease-out,-webkit-transform .5s ease-out}@media (min-width:500px){.sideDrawer{display:none}}.open{-webkit-transform:translateY(0);transform:translateY(0)}.close{-webkit-transform:translateY(-100%);transform:translateY(-100%)}