.nav{display:-webkit-box;display:flex;-webkit-box-orient:horizontal;-webkit-box-direction:normal;flex-flow:row;list-style-type:none;padding:0}.nav li{margin:10px}.nav li a{font-size:18px;letter-spacing:1px;padding:10px;text-decoration:none;border-bottom:3px solid transparent;-webkit-transition:border-bottom .5s ease-out;transition:border-bottom .5s ease-out;color:#000}.nav a:hover{border-bottom:2px solid #1fb8ea}li a.active{color:#1fb8ea}@media (max-width:600px){.nav{-webkit-box-orient:vertical;-webkit-box-direction:normal;flex-flow:column;margin-top:64px}.nav li{padding:15px;margin-bottom:10px;border-bottom:1px solid var(--light-gray)}.nav a{border:none}.nav li a:hover{border:none}li a.active{color:var(--primary-color)}}